body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden; 
  overflow-y: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.contact{
	background-color: #eceff1;
}

.ui.cards {
  flex-wrap: nowrap;
}

.card {
  min-width: 300px;
}

@media (max-width: 1280px){
  .card {
    min-width: 250px;
  }
}

@media (max-width: 900px){
	.ui.cards{
		flex-wrap: wrap;		
	}
}

*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html{
	font: normal 16px sans-serif;
	color: #555;
}

ul, nav{
	list-style: none;
}

a{
	text-decoration: none;
	color: inherit;
	cursor: pointer;

	opacity: 0.9;
}

a:hover{
	opacity: 1;
}

a.btn{
	color: #fff;
	border-radius: 4px;
	text-transform: uppercase;
	background-color: #2196F3;
	font-weight: 800;
	text-align: center;
}

hr{
	width: 150px;
	height: 2px;
	background-color: #2196F3;
	border: 0;
	margin-bottom: 80px;
}

section{
	display: flex;
	flex-direction: column;
	align-items: center;

	padding: 125px 100px;
}

@media (max-width: 1000px){

	section{
		padding: 100px 50px;
	}

}

@media (max-width: 600px){

	section{
		padding: 80px 30px;
	}

}

section h3.title{
	color: #414a4f;
	text-transform: capitalize; 
	font: bold 32px 'Open Sans', sans-serif;
	margin-bottom: 35px;
	text-align: center;
}

section p{
	max-width: 800px;
	text-align: center;
	margin-bottom: 35px;
	padding: 0 20px;
	line-height: 2;
}

ul.grid{
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}
header{
  position: absolute;
  background-color: #002029;
  padding-bottom: 20px;
	top: 0;
	left: 0;
	z-index: 10;
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: right;
	color: #fff;
	padding: 25px 100px 25px;
}

header h2{
	font-family: 'Quicksand', sans-serif;
}

header nav{
	display: flex;
}

header nav li{
  margin: 0 15px;
  font-size: 1.5rem;
  font-weight: bold;
}

header nav li a:hover{
  color: yellow;
}

.active-link {
  color: #b8cc00;
}

header nav li:first-child{
	margin-left: 0;	
}

header nav li:last-child{
	margin-right: 0;	
}


@media (max-width: 1000px){
	header{
		padding: 20px 50px;
	}
}


@media (max-width: 700px){
	header{
  justify-content: space-around
	}

	header h2{
		margin-bottom: 15px;
  }
  
  header nav li{
    font-size: 1rem;
    font-weight: bold;
  }
}


@media (max-width: 500px){
	header{
  justify-content: space-around
	}

	header h2{
		margin-bottom: 15px;
  }
  
  header nav li{
    font-size: 0.8rem;
    font-weight: bold;
    padding: 10px 0 10px 0;
  }
}

.hero{
	position: relative;
	justify-content: center;
	min-height: 100vh;
	color: #fff;
  text-align: left;
  background: rgba(27, 48, 57, 0.9);
  
}

.hero .background-image{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-size: cover;
	z-index: -1;
}

.hero .background-image:after{
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
  height: 100%;
	background-image: url(/portfolio-react/static/media/background.856df223.jpg);
}

.hero .links i {
  text-align: left;
  padding-right: 10px;
  color: #666;
}

.hero .links i:hover {
  color: #b8cc00; 
}

.hero .links i:active {
  color: yellow;
}

.hero h1{
  font: bold 60px 'Open Sans', sans-serif;
  color: #b8cc00;
	margin-bottom: 15px;
}

.hero h3{
	font: normal 28px 'Open Sans', sans-serif;
	margin-bottom: 40px;
}

.hero a.btn{
	padding: 20px 46px;
}

@media (max-width: 800px){

	.hero{
		min-height: 600px;
	}

	.hero h1{
		font-size: 48px;
	}

	.hero h3{
		font-size: 24px;
	}

	.hero a.btn{
		padding: 15px 40px;
	}

}

.Ð¾ur-work{
  background-color: #fff;
  width: 100vw;
}

.project-images {
  height: auto;
  width: 100%
}

.our-work .grid li{
  padding: 20px;
  width: 80vw;
  flex-basis: 32%;  
	border-radius: 3px;
	background-clip: content-box;
  background-size: cover;
  background-repeat: no-repeat;
	background-position: center;
	background-color: #FFF;
}

@media (max-width: 1150px){

	.our-work .grid li{
    flex-basis: 50%;
    width: 40vw;
	}
}

@media (max-width: 1000px){

	.our-work .grid li{
    flex-basis: 50%;
    width: 100vw;
	}
}

@media (max-width: 600px){

	.our-work .grid li{
    flex-basis: 100%;
    width: 100vw;    
	}
}
.skills{
  background-color: #b0bec5;
  
}

.ui.active.progress.languages .bar  {
  background: #62757f;
}

.ui.active.progress.frameworks .bar  {
  background: #4b636e;
}

.ui.active.progress.dev-tools .bar  {
  background: #1E3138;
}


.skills .grid li{
	padding: 0 30px;
	flex-basis: 33%;
  text-align: center;
  min-width: 350px;
}

.category-icon{
    font-size: 50px;
    margin-bottom: 25px;
}

.category-icon.languages {
  color: #62757f;
}

.category-icon.frameworks {
  color:#4b636e;
}

.category-icon.dev-tools {
  color: #1E3138
}



.skills .grid li h4{
	color: #555;
    font-size: 20px;
    margin-bottom: 25px;
}

.skills .grid li p{
    margin: 0;
}

@media (max-width: 1280px){
  .skills .grid li{
    min-width: 300px;
  }
}

@media (max-width: 1120px){
  .skills .grid li{
    min-width: 250px;
  }
}

@media (max-width: 1000px){

	.skills .grid li{
		flex-basis: 70%;
		margin-bottom: 65px;
	}

	.skills .grid li:last-child{
		margin-bottom: 0;
	}

}


@media (max-width: 600px){

	.skills .grid li{
		flex-basis: 100%;
	}

}

.my-pic {
  background-image: url(/portfolio-react/static/media/portrait.8cd98bea.jpg);
  min-height: 400px;
  background-size: contain;
  background-repeat: no-repeat;
}

.fas {
  margin-bottom: 10px;
}

.about{
  background-color: #eceff1;
}

.about .grid li{
	padding: 0 30px;
	flex-basis: 20%;
  text-align: center;
  min-width: 500px;
}

.about .grid li i{
    font-size: 50px;
    color: #1E3138;
    margin-top: 25px;
}


.about .grid li h4{
    font-size: 20px;
    margin-top: 0;
    margin-bottom: 10px;
}

.about .grid li p{
    margin: 0;
}

@media (max-width: 1280px){
  .my-pic {
    min-height: 450px;
    min-width: 450px;
    background-position: center;
  }

  .about .grid li{
    min-width: 380px;
  }
}

@media (max-width: 1000px){

  .my-pic {
    min-height: 200px;
    background-position: center;
  }

	.about .grid li{
		flex-basis: 70%;
		margin-bottom: 65px;
	}

	.about .grid li:last-child{
		margin-bottom: 0;
	}

}


@media (max-width: 600px){

	.about .grid li{
		flex-basis: 100%;
	}

}

